"use client";

import { useEffect, useState } from "react";
import ThemeToggle from "./components/ThemeToggle";
import ModeToggle from "./components/ModeToggle";

export default function Home() {
  const [mode, setMode] = useState("create");
  const [showSplash, setShowSplash] = useState(true);

  useEffect(() => {
    const visited = localStorage.getItem("hasVisited");
    if (visited) {
      setShowSplash(false);
    }
  }, []);

  const handleGetStarted = () => {
    localStorage.setItem("hasVisited", "true");
    setShowSplash(false);
  };

  return (
    <div className="relative min-h-screen flex flex-col justify-center items-center overflow-hidden">
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_top_left,var(--color-purple)_0%,transparent_70%)] blur-3xl opacity-30"></div>
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_bottom_right,var(--color-pink)_0%,transparent_70%)] blur-3xl opacity-30"></div>

      {/* Splash Screen */}
      {showSplash && (
        <div className="splash-container z-20 flex flex-col items-center justify-center text-center bg-(--color-bg)/80 backdrop-blur-lg border border-(--color-line) p-12 rounded-3xl shadow-2xl max-w-md">
          <div className="splash-content">
            <h1 className="text-4xl font-bold mb-3 text-(--color-fg) tracking-tight">
              Welcome to RST
            </h1>
            <div className="splash-underline"></div>
            <p className="text-(--color-comment) mb-8 mt-6 text-sm leading-relaxed">
              Create or join rooms instantly.
              <br />
              No sign-ups. No passwords.
            </p>
            <button
              onClick={handleGetStarted}
              className="splash-button px-8 py-3 rounded-xl bg-(--color-purple) text-white font-semibold hover:bg-(--color-pink) transition-colors shadow-lg"
            >
              Get Started â†’
            </button>
          </div>
        </div>
      )}

      {/* Main UI (unchanged) */}
      {!showSplash && (
        <>
          <div className="flex md:flex-row flex-col-reverse absolute top-8 right-8 gap-4">
            <ModeToggle mode={mode} setMode={setMode} />
            <ThemeToggle />
          </div>

          {mode === "join" ? (
            <div className="z-10 bg-(--color-bg)/70 backdrop-blur-lg border border-(--color-line) px-8 py-10 rounded-2xl shadow-xl hover:shadow-(--color-purple)/30 transition-all w-80">
              <div className="flex flex-col gap-6">
                Enter Room
                <div>
                  <label className="text-(--color-comment)">Username</label>
                  <input
                    className="w-full bg-transparent border-b border-(--color-comment) focus:border-(--color-green) text-(--color-fg) py-2 outline-none placeholder-(--color-comment)"
                    placeholder="Enter your username"
                  />
                </div>
                <div>
                  <label className="text-(--color-comment)">Room Id</label>
                  <input
                    className="w-full bg-transparent border-b border-(--color-comment) focus:border-(--color-pink) text-(--color-fg) py-2 outline-none placeholder-(--color-comment)"
                    placeholder="Enter Room Id"
                  />
                </div>
              </div>
            </div>
          ) : (
            <div className="z-10 bg-(--color-bg)/70 backdrop-blur-lg border border-(--color-line) px-8 py-10 rounded-2xl shadow-xl hover:shadow-(--color-purple)/30 transition-all w-80">
              <div className="flex flex-col gap-6">
                Create Room
                <div>
                  <label className="text-(--color-comment)">Username</label>
                  <input
                    className="w-full bg-transparent border-b border-(--color-comment) focus:border-(--color-green) text-(--color-fg) py-2 outline-none placeholder-(--color-comment)"
                    placeholder="Enter your username"
                  />
                </div>
                <div>
                  <label className="text-(--color-comment)">Max Players</label>
                  <input
                    className="w-full bg-transparent border-b border-(--color-comment) focus:border-(--color-pink) text-(--color-fg) py-2 outline-none placeholder-(--color-comment)"
                    placeholder="Max number of RST buddies"
                  />
                </div>
              </div>
            </div>
          )}
        </>
      )}

      <style jsx>{`
        @keyframes splashFadeIn {
          from {
            opacity: 0;
            transform: translateY(-20px) scale(0.95);
          }
          to {
            opacity: 1;
            transform: translateY(0) scale(1);
          }
        }

        @keyframes contentSlideUp {
          from {
            opacity: 0;
            transform: translateY(30px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @keyframes underlineGrow {
          from {
            width: 0;
          }
          to {
            width: 60px;
          }
        }

        @keyframes buttonFloat {
          0%,
          100% {
            transform: translateY(0);
          }
          50% {
            transform: translateY(-5px);
          }
        }

        .splash-container {
          animation: splashFadeIn 0.8s cubic-bezier(0.16, 1, 0.3, 1);
        }

        .splash-content {
          animation: contentSlideUp 1s cubic-bezier(0.16, 1, 0.3, 1) 0.2s
            backwards;
        }

        .splash-underline {
          height: 3px;
          background: linear-gradient(
            90deg,
            var(--color-purple),
            var(--color-pink)
          );
          border-radius: 2px;
          margin: 0 auto;
          animation: underlineGrow 0.6s ease-out 0.4s backwards;
        }

        .splash-button {
          animation: buttonFloat 2s ease-in-out 1s infinite;
        }
      `}</style>
    </div>
  );
}
